.header {
  background-color: #052849;
  padding: 10px;
  display: block;
}

.logo {
  width: 120px;
}

.gridContainer {
  width: 100%;
}

.sideContainer {
  margin: 0 15px;
  height: 100%;
  text-align: center;
}

.sideProduct{
  padding: 15px 15px 15px;
}

.productImage {
  width: 65%;
}

.tags {
  padding: 5px 20px 5px !important;
  text-transform: initial !important;
  margin: 5px 5px 0 0 !important;
  color: #181818 !important;
  font-size: 11px !important;
  border: .5px solid #c7cfd9 !important;
}

.chartContainer,.tableContainer{
  margin-right: 15px;
  overflow: hidden;
}
.tableContainer {
  margin-bottom: 15px;
}

.title {
  padding-left: 15px;
}

h3 {
  line-height: 0;
}

h6 {
  color: grey;
  font-size: 12px;
  line-height: 1.5em;
}
